
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
mounted() {

this.$refs.scroll.$el.style.height = document.body.clientHeight - 74 + 'px'
},
components: {
XHeader,
InlineLoading
},
data() {
return {
pageIndex: 1,
pageSize: 15,
list: [







],
nodata: false
};
},
mounted() {
this.initdata();
},
methods: {
initdata() {
this.$ajax.post('', {
'MSH.1': 'tjjl',
'MSH.2': 'list',
"pageIndex": this.pageIndex,
"pageSize": this.pageSize,
'GRDAID':this.$root.loginInfo.grdaid,
'CHZBM': this.$root.loginInfo.CHZBM,
'token': this.$root.loginInfo.token,
}).then(data => {
console.log(data)
this.$refs.scroll.finishPullUp();
this.$refs.scroll.finishPullDown();
if (typeof data != 'undefined') { //判断是否空对象
if (data.length > 0) {

this.nodata = false;
if (this.pageIndex == 1) {
this.list = data
} else {
this.list = this.list.concat(data);
}
} else {
this.judge()
}
} else {
this.judge()
}
}).catch(error => {
this.judge()
console.log(error);
});
},
judge() { //无数据判断
if (this.pageIndex > 1) {
this.$vux.toast.text('没有更多了', 'bottom');
} else {
this.list = [];
this.nodata = true;
}
},
pullingDown() {
this.pageIndex = 1;
this.initdata();
},
pullingUp() {
this.pageIndex++;
this.initdata();
},
tonext(code) {
this.$router.push({
name: 'physicalMenu',
params: {
code: code,
}
})
}
},
};



